button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding: 0;
	margin-right: 16px;

	height: 48px;
	outline: none;
}
button:last-of-type {
	margin-right: 0;
}
button.primary, button.secondary {
	padding: 0 16px;
	border-radius: 4px;
}
button.primary > svg path {
	fill: #FFF;
}
button.secondary > svg path {
	fill: #FF0088;
}
button.text > svg path {
	fill: #FF0088;
}
button .label-small {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	font-size: 15px;
	line-height: 18px;
}

/* Primary buttons */
button.primary {
	background-color: #FF0088;
	border: 0;
	color: #FFFFFF;
}
button.primary .label-small {
	color: #FFFFFF;
}

/* Secondary buttons */
button.secondary {
	background-color: rgba(0, 0, 0, 0);
	border: 1px solid #FF0088;
	color: #FF0088;
}
button.secondary .label-small {
	color: #FF0088;
}

/* Text buttons */
button.text {
	background-color: rgba(0, 0, 0, 0);
	border: none;
	color: #FF0088;
}
button.text .label-small {
	color: #FF0088;
}


